.page-footer {
	width: 100%;
	max-width: 100%;
	background: rgba(247, 247, 247, 1);
	min-height: 359px;
}

.page-footer__wrapper {
	max-width: 1200px;
	margin: 0 auto;
}

.page-footer__wrap-main {
	padding: 85px 21px 0;
	display: flex;
}

.footer {
	display: flex;
}

.footer-soc {
	margin-left: auto;
	width: 30%;
}

.footer__text {
	font-size: 15px;
    line-height: 15px;
    margin-bottom: 16px;
}

.footer__link {
	color: inherit;
	display: block;
}

.footer-soc__block-copy {
	display: flex;
	margin-bottom: 36px;
}

.footer-soc__copyright {
	font-size: 15px;
    line-height: 21px;
    text-align: right;
    margin-bottom: 8px;
}

.footer-soc__text {
	font-size: 15px;
    line-height: 21px;
    text-align: right;
}

.footer-soc__copy {
	padding-right: 24px;
	padding-top: 1px;
	padding-left: 5px;
}

.footer__block:first-of-type {
	margin-right: 159px;
}

.footer-soc__link {
	display: block;
}

.footer-soc__link:nth-child(2) {
	margin-right: 15px;
}

.footer-soc__link:nth-child(1) {
	margin-right: 16px;
}

.footer-soc__social {
	display: flex;
	justify-content: flex-end;
}