.offer {
	width: 100%;
	max-width: 100%;
	background: grey url("../img/phone-bloknot.jpg") no-repeat center;
	background-size: cover;
	min-height: 600px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;

}

.offer__wraper {
	max-width: 1200px;
	padding: 0 211px;
	
	display: flex;
	flex-direction: column;
	position: relative;
}

.offer__text {
	font-size: 35px;
    line-height: 54px;
    color: rgba(247, 247, 247, 1);
    width: 80%;
    margin-bottom: 13px;
}

.offer__btn {
	font-size: 15px;
    line-height: 40px;
    color: rgba(247, 247, 247, 1);
    background-color: rgba(126, 110, 69, 1);
    border: none;
    cursor: pointer;
    width: 182px;
    display: block;
    margin-bottom: 17px;
}

.offer__link {
	font-size: 15px;
    line-height: 22px;
    color: rgba(247, 247, 247, 1);
    display: block;
}

.offer__block-note {
	width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: rgba(126, 110, 69, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -352px;
    top: -170px;
}

.offer__note {
	font-size: 12px;
    line-height: 21px;
    color: rgba(234, 231, 217, 1);
    padding: 0 15px;
    text-align: center;
}

.offer__wrap-link {
	border-bottom: 1px solid rgba(247, 247, 247, 1);
	width: 160px;
}