.showing {
	width: 100%;
	max-width: 100%;
	background: grey url("../img/back-title.jpg") no-repeat center;
	min-height: 600px;
	background-size: cover;
	display: flex;
	align-items: flex-end;
	margin-bottom: 35px;
}

.showing__wrapper {
	max-width: 1300px;
	padding-bottom: 30px;
	padding-left: 140px;
	flex-grow: 1;
	margin: 0 auto;
}

.showing__text {
	font-size: 18px;
	line-height: 28px;
	color: rgba(255, 255, 255, 1);
	margin-bottom: 2px;
}

.showing__link {
	font-size: 15px;
	line-height: 22px;
	color: rgba(255, 255, 255, 1);
	display: block;
}

.showing__wrap-link {
	width: 82px;
	border-bottom: 1px solid rgba(255, 255, 255, 1);
}