.shop {
	width: 100%;
	max-width: 100%;
	padding-top: 113px;
	padding-bottom: 115px;
}

.shop__wrap {
	max-width: 1200px;
	margin: 0 auto;
}

.shop__content {
	display: flex;
	justify-content: space-between;
	padding: 0 20px;
	margin-bottom: 70px;
}

.shop__content-block {
	display: flex;
	flex-direction: column;
}

.shop__text {
	font-size: 30px;
    line-height: 36px;
    width: 50%;
    text-align: center;
    margin: 0 auto 92px;
}

.shop__link-image {
	margin-bottom: 7px;
	font-size: 0;
}

.shop__link {
	font-size: 16px;
    line-height: 24px;
    color: inherit;
    display: block;
}

.shop__link-decor {
	border-bottom: 1px solid rgba(26, 26, 26, 1);
}

.frame-man {
	width: 97px;
}

.frame-woman {
	width: 119px;
}

.frame-objects {
	width: 112px;
}

.shop__panorama {
	padding: 0 20px;
}

.shop__panorama .shop__link-image,
.shop__content .shop__link-image {
	display: block;
}