.collection {
	width: 100%;
	max-width: 100%;
	background-color: rgba(49, 47, 46, 1);
	min-height: 690px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.collection__wrap {
	max-width: 1200px;
	margin: 0 auto;
	display: flex;
}

.collection__wrap-content {
	display: flex;
	flex-direction: column;
	padding: 90px 95px 30px 94px;
}

.collection__content {
	max-width: 406px;
	background-color: rgba(247, 247, 247, 1);
}

.collection__subtitle {
	font-size: 38px;
    line-height: 46px;
    width: 80%;
    margin-bottom: 20px;
}

.collection__text {
	font-size: 15px;
    line-height: 21px;
    margin-bottom: 32px;
}

.collection__wrap-link {
	width: 137px;
	border-bottom: 1px solid #1A1A1A;
}

.collection__link {
	font-size: 14px;
    line-height: 21px;
    color: inherit;
    display: block;
}

.collection__image {
	font-size: 0;
}

