.page-header {
	width: 100%;
	max-width: 100%;
	min-height: 160px;
}

.page-header__wrapper {
	max-width: 1350px;
	margin: 0 auto;
	display: flex;
	padding-top: 39px;
	padding-left: 2px;
}

.page-header__item {
	font-size: 16px;
	line-height: 20px;
	color: rgba(42, 42, 42, 1);
	margin-right: 30px;
}

.page-header__item:nth-child(1) {
	margin-right: 34px;
}

.page-header__item:nth-child(2) {
	margin-right: 35px;
}

.page-header__item:nth-child(3) {
	margin-right: 38px;
}

.page-header__item:nth-child(4) {
	margin-right: 0;
}

.page-header__link {
	font-size: 14px;
	line-height: 18px;
	margin-right: 27px;
	position: relative;
	color: rgba(42, 42, 42, 1);
}

.page-header__link::after {
	content: "";
	position: absolute;
	width: 1px;
	height: 20px;
	background-color: rgba(42, 42, 42, 1);
	right: -16px;
	top: -1px;
}

.page-header__search {
	font-size: 15px;
	line-height: 18px;
	cursor: pointer;
	margin-right: 30px;
	
	border: none;
	background-color: transparent;
}

.page-header__list {
	display: flex;
	padding-left: 151px;
	padding-top: 4px;
}

.page-header__logo {
	margin-right: 3px;
}

.page-header__nav {
	flex-grow: 3;
}

.page-header__block-input {
	width: 320px;
	height: 63px;
	box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
	display: flex;
	justify-content: center;
	align-items: center;
}

.page-header__input {
	width: 300px;
	height: 43px;
	border: 1px solid #DDDDDD;
	box-sizing: border-box;
}

.page-header__button {
	cursor: pointer;
	position: relative;
	background-color: transparent;
	border: none;
}

.page-header__button::after {
	content: "";
	position: absolute;
	background-image: url("../img/basket.svg");
	width: 18px;
	height: 18px;
	top: -17px;
}

.page-header__form {
	padding-right: 78px;
	padding-top: 5px;
}

.page-header__block-btn {
	padding-left: 58px;
	display: flex;
	align-items: flex-end;
}

.page-header__login {
	margin-right: 25px;
	position: relative;
	color: rgba(42, 42, 42, 1);
}

.page-header__login::after {
	content: "";
	position: absolute;
	width: 1px;
	height: 20px;
	background-color: rgba(42, 42, 42, 1);
	right: -13px;
	top: -1px;
}

.page-header__list a {
	color: rgba(42, 42, 42, 1);
}