.products {
	width: 100%;
	max-width: 100%;
	padding-top: 5px;
	margin-bottom: 172px;
}

.products__wrapper {
	max-width: 1200px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
}

.products__btn {
	cursor: pointer;
	background-color: transparent;
	border: none;
	font-size: 17px;
	line-height: 27px;
	padding-bottom: 5px;
	color: rgba(179, 179, 179, 1);
}

.products__btn--active {
	border-bottom: 1px solid rgba(26, 26, 26, 1);
	color: inherit;
}

.products__breadcrumbs {
	margin: 0 auto;
	width: 67%;
	display: flex;
	margin-bottom: 49px;
}

.products__btn-wrap {
	padding-right: 40px;
	padding-left: 10px;
	flex-grow: 1;
	display: flex;
	justify-content: space-around;
}

.products__content {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	padding: 0 9px;
	margin-bottom: 64px;
}

.products__link {
	display: flex;
	justify-content: center;
	flex-direction: column;
	position: relative;
}

.products__link img {
	align-self: center;
	margin-bottom: 11px;

}

.products__name {
	font-size: 15px;
	line-height: 18px;
	color: rgba(26, 26, 26, 1);
	margin-bottom: 2px;
}

.products__desc {
	font-size: 14px;
	color: rgba(168, 168, 168, 1);
}

.products__cost {
	font-size: 13px;
	line-height: 13px;
	color: rgba(26, 26, 26, 1);
}

.products__discount {
	color: rgba(255, 52, 81, 1);
	font-size: 13px;
	line-height: 13px;
}

.last-cost {
	color: rgba(128, 128, 128, 1);
	margin-right: 3px;
}

.products__block-text {
	padding-left: 11px;
}

.products__card {
	margin-bottom: 29px;
}

.products__card:nth-child(12) .products__name {
	margin-bottom: 21px;
}

.products__card:nth-child(13) .products__desc,
.products__card:nth-child(15) .products__desc {
	margin-bottom: 3px;
}

.products__offer-link {
	font-size: 20px;
	line-height: 56px;
	display: block;
	width: 302px;
	min-height: 56px;
	background-color: rgba(26, 26, 26, 1);
	color: rgba(247, 247, 247, 1);
	text-align: center;
	margin: 0 auto
}

.products__block-element {
	position: absolute;
	top: 10px;
	left: 20px;
	width: 60px;
	height: 40px;
	background-color: rgba(236, 211, 202, 1);
}

.products__wrap-element {
	position: relative;
	top: 50%; 
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
}

.products__element {
	font-size: 12px;
	line-height: 12px;
	color: rgba(26, 26, 26, 1);
}

