.story {
	width: 100%;
	max-width: 100%;
	background: grey url(../img/letters-phone.jpg) no-repeat center;
	background-size: cover;
	min-height: 600px;
	display: flex;
	align-items: center;
}

.story__wrap {
	max-width: 1200px;
	margin: 0 auto;
}

.story__text {
	font-size: 35px;
    line-height: 54px;
    color: rgba(255, 255, 255, 1);
    text-align: center;
    width: 75%;
    margin: 0 auto;
}

.story__link {
	font-size: 15px;
    line-height: 22px;
    color: rgba(255, 255, 255, 1);
    display: block;
}

.story__block-link {
	border-bottom: 1px solid rgba(255, 255, 255, 1);
	width: 110px;
	padding-left: 7px;
}

.story__wrap-content {
	padding: 0 20px;
	display: flex;
	flex-direction: column;
	align-items: center;
}