.events {
	width: 100%;
	max-width: 100%;
	background: rgba(206, 211, 202, 1);
	min-height: 443px;
	
}

.events__wrapper {
	max-width: 1200px;
	padding-top: 82px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.events__subtitle {
	font-size: 34px;
    line-height: 41px;
    margin-bottom: 20px;
}

.events__text {
	font-size: 16px;
    line-height: 22px;
    margin-bottom: 30px;
    width: 50%;
    text-align: center;
    margin-bottom: 57px;
}

.events__input {
	width: 338px;
	height: 48px;
	box-sizing: border-box;
}

.events__btn {
	width: 66px;
	height: 48px;
	background-color: rgba(255, 255, 255, 1);
	cursor: pointer;
}

.events__field {
	display: flex;
	justify-content: center;
	align-items: center;
}